<template lang="pug">
.productInfo
    img-scale(v-if="showImg", @clickIt="viewImg", :imgSrc="imgSrc")
    .product-wrapper(ref="productWrapper")
        div
            article.slide_wrap
                mt-swipe(@change="handleChange", :auto="0")
                    mt-swipe-item(v-for="(img, index) in product.imgs", :key="index")
                        img(:src="img", @click="clickImg($event)")
            article.prop_detail
                .price_info ￥
                    strong.cur_price {{product.nowPrice}}
                    span.right
                        span.cost_price.price-line ￥ {{product.oldPrice}}
                        span.buyNum {{product.buyNum}}人已购买
                .count_down
                    .left 距结束仅剩
                    span {{hour}}
                    span {{min}}
                    span {{sec}}
            
            article.desc_wrap.border-line
                span.desc_icon 自营
                .div {{product.detail}}
            .additional-info.border-line
                .wrap.border-1px.hasline
                    .type 分期
                    .content
                        span.detail 满1元即可加钱换购
                        span.arrow-icon-right
                            img(src="../../assets/icon/arrow_right.png")
                .wrap.border-1px.hasline
                    .type 促销
                    .content
                        span.detail 该商品最多可享12期分期购买福利
                        span.arrow-icon-right
                            img(src="../../assets/icon/arrow_right.png")
                .wrap.border-1px.hasline
                    .type 运费
                    .content
                        span.detail 本商品满299元或2件包邮
                        span.arrow-icon-right
                            img(src="../../assets/icon/arrow_right.png")
                .wrap
                    .type 说明
                    .content
                        span.detail.detail-hook
                            <span class="detail detail-hook">
                                <img src="../../assets/icon/hook.png" alt="">税费说明
                            </span>
                        span.arrow-icon-right
                            img(src="../../assets/icon/arrow_right.png")
            article.choose-info.border-line
                span.title_name 请选择
                span.select_value 型号
                span.arrow-icon-right   
                    img(src="../../assets/icon/arrow_right.png")
</template>

<script>
import BScroll from 'better-scroll'
import ImgScale from '@/components/ImgScale.vue'
export default {
  data () {
    return {
        showImg: false,
        imgSrc: '',
        hour: 10,
        min: 30,
        sec: 60,
    }
  },
  created () {
      if(this.$store.state.tabHidden) {
          this.$store.dispatch('tabHidden')
      }
      this.interval = setInterval(() => {
          if(this.sec > 0) {
              let newSec = Math.round((this.sec - 0.1) * 10) / 10 + ''
              if(newSec.split('.')[0].length == 1) {
                  newSec = '0' + newSec
              }
              if((newSec).split('.'[1]) == undefined) {
                  this.sec = newSec + '.0'
              } else {
                  this.sec = newSec
              }
          } else {
              this.sec = 60
              if(this.min > 0) {
                  this.min --
              } else {
                  this.min = 60
                  this.hour --
              }
              if(this.hour == 0 && this.min == 0 && this.sec == 0) {
                  clearInterval(this.interval)
              }
          }
      }, 100)
  },
  components: {
    ImgScale
  },
  computed: {
      product() {
        //   等待数据渲染完成再加载
        this.$nextTick(() => {
            this.pageScroll = new BScroll(this.$refs.productWrapper, {
                click: true
            })
        })
        // 或者使用mapGatters需要在computed里面会随着依赖的改变而改变，但是data智慧在创建时候声明一次
        return this.$store.state.productInfo;
      }
  },
  methods: {
      viewImg() {
          this.showImg = false
      },
      clickImg(e) {
        //   获得当前点击的图片地址
        this.showImg = true
        this.imgSrc = e.currentTarget.src //获得当前的点击项目，e.currentTarget
      },
      handleChange() {

      }
  }
}
</script>

<style lang="stylus" type="stylesheet/stylus" scoped>
@import '../../common/stylus/mixin.styl'
.productInfo
  position absolute
  left 0
  right 0
  bottom 1.4rem
  top 1.24rem
  width 100%
  display flex
  overflow hidden
  box-sizing border-box
  .product-wrapper
    flex 1
    .slide_wrap
      height 10rem
      .mint-swipe-indicators.is-active
        background mainColor
        opacity 1
      .mint-swipe-item 
        width 10rem
        img
          width 10rem
          height 100%
    .prop_detail
      width 10rem
      height 1.5625rem
      background mainColor
      display flex
      color #fff
      align-items center
      padding 0rem 0.341rem
      .price_info
        font-size numFont
        position relative
        flex 1
        .cur_price
          font-size 1.09375rem
        .right
          .cost_price
            price-line(#fff,1)
            position absolute
            margin-left .1rem
            top .1rem
          .buyNum
            position absolute
            margin-left .15rem
            top .65rem
      .count_down
        font-size .3875rem
        padding-right .341rem
        flex .8
        .left
          display block
          margin-left 1.3rem
          padding-bottom .2rem
        span
          background #fff
          color #000
          display inline-block
          padding .08rem .08rem 0.1rem
          font-size numFont
          margin-right .1rem
          border-radius 20%
          text-align center
    .desc_wrap
      padding 0.341rem
      font-size mainFont
      position relative
      border-line()
      padding-bottom .5rem
      line-height .6rem
      width 9.3rem
      .desc_icon
        background mainColor
        color #fff
        line-height 1px
        font-size 0.4rem
        width 1rem
        padding .05rem
        border-radius 10%
        margin-left .1rem
    .additional-info
      padding 0rem 0.341rem
      position relative
      border-line()
      .hasline
        border-1px(92%)
      .wrap
        height 1.20625rem
        line-height 1.20625rem
        position relative
        padding-right .341rem
        font-size .4rem
        .type
          display inline-block
          color fontTint
          padding-right .8rem
        .content
          display inline-block
          .detail
            height 1.20625rem
            img
              display inline-block
              vertical-align middle
              width 8%
              padding-bottom .1rem
        .arrow-icon-right
          position absolute
          right .741rem
          top .12rem
          height .5rem
          width .5rem
          img
            height 100%
    .choose-info
      height 1.20625rem
      line-height 1rem
      position relative
      padding 0rem .341rem
      border-line()
      font-size .4rem
      color fontTint
      .select_value
        color #000
        padding-left .8rem
      .arrow-icon-right
        width .75rem
        height .75rem
        position absolute
        right 1.2rem
        top .12rem
        img
          height 100%
</style>



